import Link from "next/link";
import { Button } from "@/components/ui/button"; // Placeholder, will create UI button later

export default function Home() {
  return (
    <main className="flex min-h-screen flex-col items-center justify-center p-24">
      <div className="z-10 max-w-5xl w-full items-center justify-between font-mono text-sm lg:flex">
        <h1 className="text-4xl font-bold">Sistema de Horarios UPDS</h1>
      </div>
       <div className="mt-10">
          <p className="text-xl mb-6">Bienvenido al sistema de gestión.</p>
          <div className="flex gap-4">
             <Link href="/horario" className="px-4 py-2 bg-primary text-primary-foreground rounded-md hover:bg-primary/90">
                Ir a Horarios
             </Link>
             <Link href="/docentes" className="px-4 py-2 bg-secondary text-secondary-foreground rounded-md hover:bg-secondary/80">
                Gestión de Docentes
             </Link>
          </div>
       </div>
    </main>
  );
}
